@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
html {
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Noto Sans', sans-serif;
  font-size: 1.1rem;
  line-height: 1.5;
  color: #111;
  font-weight: 400;
  background-color: #fff; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

.home {
  max-width: 100rem;
  margin: 0 auto;
  padding: 1rem; }

.signature-list {
  display: grid;
  font-size: 0.7rem;
  grid-template-columns: 1fr;
  grid-template-areas: 'imperium' 'indestor' 'lamakler' 'rentercorp' 'welltor' 'indestor1' 'lamakler1' 'rentercorp1' 'welltor1';
  grid-gap: 1.5rem; }

.imperium {
  grid-area: imperium; }

.indestor {
  grid-area: indestor; }

.lamakler {
  grid-area: lamakler; }

.rentercorp {
  grid-area: rentercorp; }

.welltor {
  grid-area: welltor; }

.indestor1 {
  grid-area: indestor1; }

.lamakler1 {
  grid-area: lamakler1; }

.rentercorp1 {
  grid-area: rentercorp1; }

.welltor1 {
  grid-area: welltor1; }

@media (min-width: 450px) {
  .signature-list {
    font-size: 1.1rem; } }

@media (min-width: 768px) {
  .signature-list {
    display: grid;
    font-size: 1.1rem;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 'imperium indestor' 'lamakler rentercorp' 'welltor indestor1' 'lamakler1 rentercorp1' 'welltor1 .';
    grid-gap: 1.5rem; } }

@media (min-width: 1400px) {
  .signature-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: 'imperium indestor lamakler' 'rentercorp welltor indestor1' 'lamakler1 rentercorp1 welltor1';
    grid-gap: 1.5rem; } }

.top-menu {
  padding: 1rem 0 2rem; }
  .top-menu__create {
    display: flex;
    justify-content: space-between; }
  .top-menu__delete {
    display: flex;
    align-items: center; }
  .top-menu__title {
    font-size: inherit;
    font-weight: inherit; }
  .top-menu__btn {
    border: 1px solid #0097ff;
    color: #0097ff;
    outline: 0;
    background: transparent;
    padding: 0.3rem 1rem;
    cursor: pointer; }
  .top-menu__selected-tile {
    margin-right: 1rem; }
    .top-menu__selected-tile:before {
      content: '';
      display: inline-block;
      height: 1rem;
      width: 1rem;
      border: 1px solid #111;
      margin-right: 1rem;
      border-radius: 2px; }
  .top-menu__delete-btn {
    background: transparent;
    color: #0097ff;
    border: 0;
    outline: 0;
    display: flex;
    min-width: 6rem;
    justify-content: space-between;
    font-size: inherit;
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s; }
    .top-menu__delete-btn:hover {
      opacity: 0.5; }

.signature {
  box-shadow: 1px 1px 4px -1px rgba(0, 0, 0, 0.33);
  position: relative; }
  .signature__hidden-info {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#30a2f2d4), to(#024675));
    background-image: -webkit-linear-gradient(#30a2f2d4, #024675);
    background-image: linear-gradient(#30a2f2d4, #024675);
    display: flex;
    padding: 1rem;
    color: #fff;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    opacity: 0; }
    .signature__hidden-info--show {
      opacity: 1; }
  .signature__checkbox {
    position: absolute;
    opacity: 0;
    height: 0;
    width: 0; }
    .signature__checkbox:checked + .signature__checkbox-custom:after {
      content: '\2713';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%); }
  .signature__checkbox-custom {
    display: block;
    height: 1rem;
    width: 1rem;
    position: relative;
    border: solid 1px #fff;
    border-radius: 2px; }
  .signature__info-and-controls {
    padding: 0 1rem 0 0.5rem;
    align-content: space-around;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
    flex-grow: 1;
    overflow: hidden; }
    @media (min-width: 768) {
      .signature__info-and-controls {
        padding: 2rem 1rem 0 0; } }
  .signature__title {
    min-width: 9rem;
    display: inline-block; }
  .signature__data {
    text-transform: uppercase; }
  .signature__controls {
    display: flex;
    justify-content: space-between; }
  .signature__use-url {
    border: solid 1.5px #fff;
    display: inline-block;
    font-size: 1.1rem;
    padding: 0.2rem 2rem;
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s; }
    .signature__use-url:hover {
      opacity: 0.6; }
  .signature__btn-list {
    min-width: 7.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.3rem; }
  .signature__btn {
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s; }
    .signature__btn:hover {
      opacity: 0.6; }
  .signature:hover .signature__hidden-info {
    opacity: 1; }

